<template>
  <div class="registration">
    <div class="registration__title"> </div>
    <div class="select-block__item-block">
      <label for="standard-select">   </label>
      <div class="select">
        <select required id="standard-select" v-model="competition">
          <option value="  1 ">
            "  1 "
          </option>
          <option value="  2 ">
            "  2 "
          </option>
          <option value="  3 ">
            "  3 "
          </option>
        </select>
        <span class="focus"></span>
      </div>
    </div>
    <div class="registration__wrap-select">
      <div class="select-block__item-block margin-select">
        <label for="standard-select">   </label>
        <div class="select">
          <select required id="standard-select" v-model="selectBrigade">
            <option value="1"> 1</option>
            <option value="2"> 2</option>
          </select>
          <span class="focus"></span>
        </div>
      </div>
      <div class="select-block__item-block margin-select">
        <label for="standard-select"> </label>
        <div class="select">
          <select required id="standard-select" v-model="selectPosition">
            <option value="exe"></option>
            <option value="art"></option>
            <option value="dif"></option>
            <option value="arbitrator"></option>
            <option value="main"> </option>
            <option value="admin"> </option>
          </select>
          <span class="focus"></span>
        </div>
      </div>
      <div class="select-block__item-block margin-select">
        <label for="standard-select">  </label>
        <div class="select">
          <select required id="standard-select" v-model="selectPositionNumber">
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
          <span class="focus"></span>
        </div>
      </div>
    </div>
    <div class="registration__user">
      <div class="info__input margin-select">
        <label for="art"></label>
        <input
          type="text"
          id="item"
          name="art"
          placeholder=" "
          v-model="fio"
          aria-label=""
          class="input-search"
        />
      </div>
      <div class="info__input margin-select">
        <label for="art"></label>
        <input
          type="text"
          id="item"
          name="art"
          placeholder=""
          v-model="club"
          aria-label=""
          class="input-search"
        />
      </div>
    </div>
    <div class="registration__input">
      <fieldset class="form-group">
        <label for="username"> </label>
        <input
          type="text"
          class="form-control"
          placeholder=""
          name="username"
          v-model="username"
        />
      </fieldset>
      <fieldset class="form-group">
        <label for="username"> </label>
        <input
          type="password"
          class="form-control"
          name="password"
          placeholder="******"
          v-model="password"
        />
      </fieldset>
      <input
        type="button"
        value=""
        class="button btn-login"
        @click="onSubmit()"
      />
    </div>
  </div>
</template>

<script>
import { actionTypes } from "@/store/modules/auth";
// import { mapState } from "vuex";
export default {
  name: "AppRegistration",
  data() {
    return {
      fio: "",
      club: "",
      selectBrigade: "",
      selectPosition: "",
      selectPositionNumber: "",
      username: "",
      password: "",
      competition: "",
      role: "",
    };
  },
  methods: {
    onSubmit() {
      if (this.selectPosition === "exe" || this.selectPosition === "art") {
        this.role = "ROLE_EA";
      } else if (this.selectPosition === "dif") {
        this.role = "ROLE_DIF";
      } else if (this.selectPosition === "arbitrator") {
        this.role = "ROLE_ARB";
      } else if (this.selectPosition === "main") {
        this.role = "ROLE_MAIN";
      } else {
        this.role = "ADMIN";
      }
      this.$store
        .dispatch(actionTypes.registration, {
          fio: this.fio,
          club: this.club,
          brigade: this.selectBrigade,
          position: this.selectPosition,
          positionNumber: this.selectPositionNumber,
          username: this.username,
          password: this.password,
          competition: this.competition,
          role: this.role,
        })
        .then((resp) => {
          console.log(resp);
          // if (!this.validationErrors) {
          //   this.$router.push({ name: "Home" });
          // }
        })
        .catch((er) => {
          console.log("", er);
        });
    },
  },
};
</script>

